import React, { useEffect, useState } from "react"
import { darkTheme, lightTheme } from "../components/Global"
import { Helmet } from "react-helmet"
import favicon from "../images/favicon.ico"
import Layout from "../components/layout"
import styled, { ThemeProvider } from "styled-components"
import { HomeContainer, HomeNav } from "../components/Home/Nav"
import HomeFooter from "../components/Home/Footer"
import { ArticleH1, ArticleLink, ArticleTextSpan } from "../components/Article/Content"

const PPContainer = styled.div`
    display: flex;
    flex-direction: column;
    justify-contents: flex-start;
    padding: 2% 10%;
`

const PPTitle = styled(ArticleH1)`
  font-size: 48px;
`

const Contact = () => {
  const [darkMode, setDarkMode] = useState(false)
  const [theme, setTheme] = useState(lightTheme)

  useEffect(() => {
    const lsDark = localStorage.getItem("dark") === "true"
    toggleDarkMode(lsDark)

  }, [])

  const toggleDarkMode = (darkModeBool) => {
    if (darkModeBool === true) {
      setTheme(darkTheme)
    } else {
      setTheme(lightTheme)
    }
    localStorage.setItem("dark", JSON.stringify(darkModeBool))
    setDarkMode(darkModeBool)
  }

  return (
    <>
      <Helmet>
        <link rel="icon" href={favicon}/>
      </Helmet>
      <Layout>
        <ThemeProvider theme={theme}>
          <HomeContainer>
            <HomeNav darkMode={darkMode} toggleDarkMode={toggleDarkMode}/>
            <PPContainer>
              <PPTitle>Privacy Policy</PPTitle>

              <ArticleTextSpan>At KHBlogs, accessible from https://blog.krharsh17.vision, one of our main priorities is
                the privacy of
                our visitors. This Privacy Policy document contains types of information that is collected and recorded
                by KHBlogs and how we use it.</ArticleTextSpan>

              <ArticleTextSpan>If you have additional questions or require more information about our Privacy Policy, do
                not hesitate
                to contact us.</ArticleTextSpan>

              <ArticleTextSpan>This Privacy Policy applies only to our online activities and is valid for visitors to
                our website with
                regards to the information that they shared and/or collect in KHBlogs. This policy is not applicable to
                any information collected offline or via channels other than this website.</ArticleTextSpan>

              <ArticleH1>Consent</ArticleH1>

              <ArticleTextSpan>By using our website, you hereby consent to our Privacy Policy and agree to its
                terms.</ArticleTextSpan>

              <ArticleH1>Information we collect</ArticleH1>

              <ArticleTextSpan>The personal information that you are asked to provide, and the reasons why you are asked
                to provide
                it, will be made clear to you at the point we ask you to provide your personal
                information.</ArticleTextSpan>
              <ArticleTextSpan>If you contact us directly, we may receive additional information about you such as your
                name, email
                address, phone number, the contents of the message and/or attachments you may send us, and any other
                information you may choose to provide.</ArticleTextSpan>
              <ArticleTextSpan>When you register for an Account, we may ask for your contact information, including
                items such as
                name, company name, address, email address, and telephone number.</ArticleTextSpan>

              <ArticleH1>How we use your information</ArticleH1>

              <ArticleTextSpan>We use the information we collect in various ways, including to:</ArticleTextSpan>

              <ArticleTextSpan>Provide, operate, and maintain our webste</ArticleTextSpan>
              <ArticleTextSpan>Improve, personalize, and expand our webste</ArticleTextSpan>
              <ArticleTextSpan>Understand and analyze how you use our webste</ArticleTextSpan>
              <ArticleTextSpan>Develop new products, services, features, and functionality</ArticleTextSpan>
              <ArticleTextSpan>Communicate with you, either directly or through one of our partners, including for
                customer
                service, to provide you with updates and other information relating to the webste, and for marketing
                and promotional purposes
              </ArticleTextSpan>
              <ArticleTextSpan>Send you emails</ArticleTextSpan>
              <ArticleTextSpan>Find and prevent fraud</ArticleTextSpan>

              <ArticleH1>Log Files</ArticleH1>

              <ArticleTextSpan>KHBlogs follows a standard procedure of using log files. These files log visitors when
                they visit
                websites. All hosting companies do this and a part of hosting services' analytics. The information
                collected by log files include internet protocol (IP) addresses, browser type, Internet Service Provider
                (ISP), date and time stamp, referring/exit pages, and possibly the number of clicks. These are not
                linked to any information that is personally identifiable. The purpose of the information is for
                analyzing trends, administering the site, tracking users' movement on the website, and gathering
                demographic information. Our Privacy Policy was created with the help of the <ArticleLink
                  href="https://www.privacypolicygenerator.info">Privacy Policy Generator</ArticleLink> and
                the <ArticleLink
                  href="https://www.disclaimergenerator.org/">Disclaimer Generator</ArticleLink>.</ArticleTextSpan>


              <ArticleH1>Google DoubleClick DART Cookie</ArticleH1>

              <ArticleTextSpan>Google is one of a third-party vendor on our site. It also uses cookies, known as DART
                cookies, to
                serve ads to our site visitors based upon their visit to www.website.com and other sites on the
                internet. However, visitors may choose to decline the use of DART cookies by visiting the Google ad and
                content network Privacy Policy at the following URL – <ArticleLink
                  href="https://policies.google.com/technologies/ads">https://policies.google.com/technologies/ads</ArticleLink>
              </ArticleTextSpan>


              <ArticleH1>Advertising Partners Privacy Policies</ArticleH1>

              <ArticleTextSpan>You may consult this list to find the Privacy Policy for each of the advertising partners
                of
                KHBlogs.</ArticleTextSpan>

              <ArticleTextSpan>Third-party ad servers or ad networks uses technologies like cookies, JavaScript, or Web
                Beacons that
                are used in their respective advertisements and links that appear on KHBlogs, which are sent directly to
                users' browser. They automatically receive your IP address when this occurs. These technologies are used
                to measure the effectiveness of their advertising campaigns and/or to personalize the advertising
                content that you see on websites that you visit.</ArticleTextSpan>

              <ArticleTextSpan>Note that KHBlogs has no access to or control over these cookies that are used by
                third-party
                advertisers.</ArticleTextSpan>

              <ArticleH1>Third Party Privacy Policies</ArticleH1>

              <ArticleTextSpan>KHBlogs's Privacy Policy does not apply to other advertisers or websites. Thus, we are
                advising you to
                consult the respective Privacy Policies of these third-party ad servers for more detailed information.
                It may include their practices and instructions about how to opt-out of certain
                options. </ArticleTextSpan>

              <ArticleTextSpan>You can choose to disable cookies through your individual browser options. To know more
                detailed
                information about cookie management with specific web browsers, it can be found at the browsers'
                respective websites.</ArticleTextSpan>

              <ArticleH1>CCPA Privacy Rights (Do Not Sell My Personal Information)</ArticleH1>

              <ArticleTextSpan>Under the CCPA, among other rights, California consumers have the right
                to:</ArticleTextSpan>
              <ArticleTextSpan>Request that a business that collects a consumer's personal data disclose the categories
                and specific
                pieces of personal data that a business has collected about consumers.</ArticleTextSpan>
              <ArticleTextSpan>Request that a business delete any personal data about the consumer that a business has
                collected.</ArticleTextSpan>
              <ArticleTextSpan>Request that a business that sells a consumer's personal data, not sell the consumer's
                personal
                data.</ArticleTextSpan>
              <ArticleTextSpan>If you make a request, we have one month to respond to you. If you would like to exercise
                any of these
                rights, please contact us.</ArticleTextSpan>

              <ArticleH1>GDPR Data Protection Rights</ArticleH1>

              <ArticleTextSpan>We would like to make sure you are fully aware of all of your data protection rights.
                Every user is
                entitled to the following:</ArticleTextSpan>
              <ArticleTextSpan>The right to access – You have the right to request copies of your personal data. We may
                charge you a
                small fee for this service.</ArticleTextSpan>
              <ArticleTextSpan>The right to rectification – You have the right to request that we correct any
                information you believe
                is inaccurate. You also have the right to request that we complete the information you believe is
                incomplete.</ArticleTextSpan>
              <ArticleTextSpan>The right to erasure – You have the right to request that we erase your personal data,
                under certain
                conditions.</ArticleTextSpan>
              <ArticleTextSpan>The right to restrict processing – You have the right to request that we restrict the
                processing of
                your personal data, under certain conditions.</ArticleTextSpan>
              <ArticleTextSpan>The right to object to processing – You have the right to object to our processing of
                your personal
                data, under certain conditions.</ArticleTextSpan>
              <ArticleTextSpan>The right to data portability – You have the right to request that we transfer the data
                that we have
                collected to another organization, or directly to you, under certain conditions.</ArticleTextSpan>
              <ArticleTextSpan>If you make a request, we have one month to respond to you. If you would like to exercise
                any of these
                rights, please contact us.</ArticleTextSpan>

              <ArticleH1>Children's Information</ArticleH1>

              <ArticleTextSpan>Another part of our priority is adding protection for children while using the internet.
                We encourage
                parents and guardians to observe, participate in, and/or monitor and guide their online
                activity.</ArticleTextSpan>

              <ArticleTextSpan>KHBlogs does not knowingly collect any Personal Identifiable Information from children
                under the age of
                13. If you think that your child provided this kind of information on our website, we strongly encourage
                you to contact us immediately and we will do our best efforts to promptly remove such information from
                our records.</ArticleTextSpan>

            </PPContainer>
            <HomeFooter/>
          </HomeContainer>
        </ThemeProvider>
      </Layout>
    </>
  )
}

export default Contact